import {EventEmitter, Injectable} from '@angular/core';
import {ImageSnippet} from '../classes/imagesnippet';


@Injectable({
  providedIn: 'root'
})
export class ImageUploadService {
  imageChange = new EventEmitter<ImageSnippet>();
  image: ImageSnippet;

  constructor() { }

    uploadFile(image: File) {
        const selectedImage: File = image[0];

        const reader = new FileReader();
        reader.addEventListener('load', (event: any) => {
            this.image = new ImageSnippet(event.target.result, selectedImage);
            console.log(image[0]);
            this.imageChange.emit(this.image);
        });

        reader.readAsDataURL(selectedImage);
        //  console.log(image);
    }
}
