import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {FormControl, FormGroup, NgForm, Validators} from '@angular/forms';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {CustomerService} from '../../../shared/services/customer.service';
import {Customer} from '../../../shared/classes/customer';
import {Subscription} from 'rxjs';
import {ImageUploadService} from '../../../shared/services/image-upload.service';
import {ImageSnippet} from '../../../shared/classes/imagesnippet';
import {ValidateImageSrc} from '../../../shared/helpers/validateImagesrc';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit, OnDestroy {

    // @Input() id: number;
    customerId: number;
    customer: Customer;
    paramsSubscription: Subscription;
    success = false;
    status: string;
    image: ImageSnippet;
    customerData: FormData;

    // Form
    profileForm: FormGroup;
    submitted = false;

    constructor(private route: ActivatedRoute, private router: Router,
                private customerService: CustomerService, private imageUpload: ImageUploadService) {
    }

    ngOnInit() {
        this.paramsSubscription = this.route.queryParams.subscribe((queryParams: Params) => {
            this.customerId = queryParams.id ? queryParams.id : null;
            console.log(this.customerId);
        });

        this.customerService.getCustomers.subscribe((customers: any) => {
            this.customer = customers.find(item => item.customerId == this.customerId);

            console.log(this.customer);

            if (!this.customer) { this.router.navigate(['pages/404']); }
        });

        this.profileForm = new FormGroup({
            firstName: new FormControl(this.customer.customerFirstName, [Validators.required]),
            lastName: new FormControl(this.customer.customerLastName, [Validators.required]),
            title: new FormControl(this.customer.title, [Validators.required]),
            image: new FormControl(this.customer.imageurl, [Validators.required], ValidateImageSrc.imageRequired),
            email: new FormControl(this.customer.email, [Validators.required, Validators.email]),
            gender: new FormControl(this.customer.gender, [Validators.required]),
            profile: new FormControl(this.customer.profile),
            phone: new FormControl(this.customer.phone, [Validators.required]),
            address1: new FormControl(this.customer.address1, [Validators.required]),
            address2: new FormControl(this.customer.address2),
            city: new FormControl(this.customer.city, [Validators.required]),
            state: new FormControl(this.customer.state, [Validators.required]),
            zipCode: new FormControl(this.customer.zipCode, [Validators.required]),
            country: new FormControl(this.customer.country, [Validators.required])
        });

        this.imageUpload.imageChange.subscribe((image: ImageSnippet) => {
            this.customer.imageurl = image.src;
            console.log(image.file.name);
            this.profileForm.get('image').patchValue(image.file.name as string);
        });
    }

    // convenience getter for easy access to form fields
    get f() { return this.profileForm.controls; }

    getControlValue(control) {
        return this.profileForm.get(control).value;
    }

    private onSuccess() {
        this.status = 'ok';
    }

    private onError() {
        // this.image.src = '../../../../assets/images/placeholder-80x80.png';
        this.status = 'fail';
    }

    getFile(image: File) {
        this.imageUpload.uploadFile(image);
    }

    onSubmit() {
        this.submitted = false;
        // stop here if form is invalid
        if (this.profileForm.invalid) {
            return;
        }

        this.customerData = new FormData();

        this.customerData.append('firstName', this.getControlValue('firstName'));
        this.customerData.append('lastName', this.getControlValue('lastName'));
        this.customerData.append('title', this.getControlValue('title'));
        this.customerData.append('image', this.image ? this.image.file : this.customer.imageurl );
        this.customerData.append('email', this.getControlValue('email'));
        this.customerData.append('gender', this.getControlValue('gender'));
        this.customerData.append('profile', this.getControlValue('profile'));
        this.customerData.append('phone', this.getControlValue('phone'));
        this.customerData.append('address1', this.getControlValue('address1'));
        this.customerData.append('address2', this.getControlValue('address2'));
        this.customerData.append('city', this.getControlValue('city'));
        this.customerData.append('state', this.getControlValue('state'));
        this.customerData.append('zipCode', this.getControlValue('zipCode'));
        this.customerData.append('country', this.getControlValue('country'));

        this.customerService.saveCustomer(this.customerData).subscribe(
            (resp) => { console.log(resp); this.onSuccess(); this.success = true; setTimeout(() => { this.success = !this.success; this.status = ''; this.image = null; }, 6000); },
            (error) => { console.log(error); this.onError(); }
        );

        // display form values on success
        alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.profileForm.value, null, 4));
        this.onReset();
    }

    onReset() {
        this.submitted = false;
        this.profileForm.reset();
        this.profileForm.patchValue({ tiltle: ''});
        this.profileForm.patchValue({ gender: ''});
        this.profileForm.patchValue({ country: ''});
    }

    ngOnDestroy() {
        this.paramsSubscription.unsubscribe();
    }

}
