import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Categories, Category} from '../classes/category';
import {map, startWith} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {
  Categories: Observable<Category[]>;
  products;

  constructor(private httpClient: HttpClient) {
  }

  protected get categories(): Observable<Category[]> {
      this.Categories = this.httpClient.get<Categories>('/api/categories/getCategories').pipe(map(data => data.categories));
      this.Categories.subscribe(next => {
          localStorage.categories = JSON.stringify(next);
      });
      return this.Categories = this.Categories.pipe(startWith(JSON.parse(localStorage.categories || '[]')));
  }

  public get getCategories(): Observable<Category[]> {
      return this.categories;
  }
}
