import {Component, OnInit, OnDestroy} from '@angular/core';
import {CategoryService} from '../../shared/services/category.service';
import {Category} from '../../shared/classes/category';
import {toMatrix} from '../../shared/helpers/listtomatrix';
import {ActivatedRoute, Router} from '@angular/router';
import {ProductService} from '../../shared/services/product.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'app-collection',
  templateUrl: './collection.component.html',
  styleUrls: ['./collection.component.scss']
})

export class CollectionComponent implements OnInit, OnDestroy {
    categories: Category[] = [];
    products;
    private Subscription: Subscription;

    constructor(private categoryService: CategoryService, private route: ActivatedRoute, private router: Router,
                public productService: ProductService) {

    }

    ngOnInit(): void {

        this.Subscription = this.productService.getProducts.subscribe(products => this.products = products );

        this.categoryService.getCategories.subscribe((categoryItems: Category[]) => {
            this.categories =  toMatrix(categoryItems.map((item, i) => Object.assign({}, item, this.getCategoriesAndStock[i])), 4);
        });
    }

    navigateTo(category: string) {
        this.router.navigate(['/shop/collection/products'], {queryParams: { category}});
    }

    public get getCategoriesAndStock() {
        return Array.from(this.products.reduce(
            (m, {category, stock}) => m.set(category, (m.get(category) || 0) + stock), new Map
        ), ([category, stock]) => ({category, stock}));
    }

    ngOnDestroy(): void {
        this.Subscription.unsubscribe();
    }
}
