import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {Customer, Customers} from '../classes/customer';

@Injectable({
  providedIn: 'root'
})

export class CustomerService {
    public Customers;

  constructor(private http: HttpClient) { }

    // Customers
    protected get customers(): Observable<Customer[]> {
        this.Customers = this.http.get<Customers>('/api/customers/getCustomers').pipe(map(data => data.customers));
        this.Customers.subscribe(next => { localStorage.customers = JSON.stringify(next); });
        return this.Customers = this.Customers.pipe(startWith(JSON.parse(localStorage.customers || '[]')));
    }

    // Get Customers
    public get getCustomers(): Observable<Customer[]> {
        return this.customers;
    }

    saveCustomer(customer: any) {
        return this.http.post('http://httpbin.org/post', customer);
    }
}
