import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, NgForm, Validators} from '@angular/forms';
import {CustomerService} from '../../../shared/services/customer.service';
import {ImageUploadService} from '../../../shared/services/image-upload.service';
import {MustMatch} from '../../../shared/helpers/mustmatch';
import {ImageSnippet} from '../../../shared/classes/imagesnippet';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})

export class RegisterComponent implements OnInit {
    // @ViewChild('registerForm') registerForm: NgForm;

    registerForm: FormGroup;
    genders = ['male', 'female'];
    success = false;
    status: string;
    image: ImageSnippet;
    submitted = false;
    customerData: FormData;

    constructor(private customerProfile: CustomerService, private imageUpload: ImageUploadService, private formBuilder: FormBuilder) {
        this.registerForm = this.formBuilder.group({
            title: [null, Validators.required],
            firstName: [null, Validators.required],
            lastName: [null, Validators.required],
            image: [null, Validators.required],
            gender: ['male', Validators.required],
            email: [null, [Validators.required, Validators.email]],
            password: [null, [Validators.required, Validators.minLength(6), Validators.maxLength(12)]],
            confirmPassword: [null, Validators.required],
            acceptTerms: [false, Validators.requiredTrue]
        }, {
            validator: MustMatch('password', 'confirmPassword')
        });
    }

    // convenience getter for easy access to form fields
    get f() { return this.registerForm.controls; }

    getControlValue(control) { return this.registerForm.get(control).value; }

    ngOnInit() {
        this.imageUpload.imageChange.subscribe((image: ImageSnippet) => { this.image = image; console.log(image); this.registerForm.get('image').setValue(this.image.file.name); });
    }


    private onSuccess() {
        this.status = 'ok';
    }

    private onError() {
        this.image.src = '../../../../assets/images/placeholder-80x80.png';
        this.status = 'fail';
    }

    getFile(image: File) {
        this.imageUpload.uploadFile(image);
    }

    //
    onSubmit() {
        this.submitted = false;
        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }

        this.customerData = new FormData();

        this.customerData.append('firstName', this.getControlValue('firstName'));
        this.customerData.append('lastName', this.getControlValue('firstName'));
        this.customerData.append('title', this.getControlValue('title'));
        this.customerData.append('image', this.image.file);
        this.customerData.append('email', this.getControlValue('email'));
        this.customerData.append('password', this.getControlValue('password'));
        this.customerData.append('gender', this.getControlValue('gender'));
        this.customerData.append('acceptTerms', this.getControlValue('acceptTerms'));

        this.customerProfile.saveCustomer(this.customerData).subscribe(
            (resp) => { console.log(resp); this.onSuccess(); this.success = true; setTimeout(() => { this.success = !this.success; this.status = ''; this.image = null; }, 6000); },
            (error) => { console.log(error); this.onError(); }
        );

        // display form values on success
        alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.registerForm.value, null, 4));
        this.onReset();
    }

    onReset() {
        this.submitted = false;
        this.registerForm.reset();
        this.registerForm.patchValue({ gender: 'male'});
    }
}
